<script setup lang="ts">
/**
 * This component is used at the bottom of the RightDrawer component
 * to provide feedback from the user's actions.
 *
 * It is wrapped in a Transition component to provide a slide in/fade
 * out animation, and fades out after 3 seconds. The onMounted
 * and onUnmounted hooks are used to clear the timeout when the
 * component is destroyed - this is important to control the lifecycle
 * of the timeout when the user clicks the same action multiple times.
 */
import { onMounted, onUnmounted } from 'vue'
import { useUi } from '@/shared/lib/composables/useUi'
import type { IAction } from '@/shared/types/api/IAction'
import IconCloseCircle from '@/shared/ui/icons/IconCloseCircle.vue'

const { getActionErrorMessage, resetActionsInError } = useUi()

defineProps<{
  action: IAction
}>()

let timeout: ReturnType<typeof setTimeout>

onMounted(() => {
  timeout = setTimeout(() => {
    resetActionsInError()
  }, 3000)
})

onUnmounted(() => {
  clearTimeout(timeout)
})
</script>

<template>
  <div class="pointer-events-none absolute inset-x-0 bottom-0">
    <div class="max-w-[65%] rounded-md border border-red-100 bg-red-50 p-3 md:p-4">
      <div class="flex">
        <div class="shrink-0">
          <IconCloseCircle class="h-5 w-5 fill-red-400" aria-hidden="true" />
        </div>
        <div class="ml-3">
          <h3 class="text-sm font-medium text-red-800">
            Oops!
          </h3>
          <div class="mt-2 text-sm text-red-700">
            <ul role="list" class="list-disc space-y-1 pl-5">
              <li v-html="getActionErrorMessage(action)" />
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
